"use client";

import { useState } from "react";
import { Store, Settings, Download, Filter, ChevronDown } from "lucide-react";
import { Montserrat } from "next/font/google";
import { toast } from "sonner";

const montserrat = Montserrat({
  subsets: ["latin"],
  weight: "800",
  display: "swap",
});

interface Sale {
  id: number;
  productName: string;
  quantity: number;
  colors: string[];
  sizes: string[];
  clientName: string;
  clientEmail: string;
  date: string;  // Ajouté pour la date d'achat
  total: number; // Ajouté pour le montant total
  productImage: string;
}

export default function SalesPage() {
  const [sales, setSales] = useState<Sale[]>([
    {
      id: 1,
      productName: "Produit 1",
      quantity: 2,
      colors: ["Noir"],
      sizes: ["M"],
      clientName: "Ahmed Benali",
      clientEmail: "ahmed@example.com",
      date: "2024-03-15", // Ajouté
      total: 150.00,      // Ajouté
      productImage: "https://images.unsplash.com/photo-1542291026-7eec264c27ff?ixlib=rb-4.0.3&auto=format&fit=crop&w=100&q=80",
    },
    {
      id: 2,
      productName: "Produit 2",
      quantity: 1,
      colors: ["Blanc"],
      sizes: ["L"],
      clientName: "Sara Khedir",
      clientEmail: "sara@example.com",
      date: "2024-03-16", // Ajouté
      total: 75.50,       // Ajouté
      productImage: "https://images.unsplash.com/photo-1506157786151-b8491531f063?ixlib=rb-4.0.3&auto=format&fit=crop&w=100&q=80",
    },
    {
      id: 3,
      productName: "Produit 3",
      quantity: 3,
      colors: ["Gris", "Bleu"],
      sizes: ["S", "M"],
      clientName: "Yanis Merad",
      clientEmail: "yanis@example.com",
      date: "2024-03-17", // Ajouté
      total: 225.00,      // Ajouté
      productImage: "https://images.unsplash.com/photo-1441986300917-64674bd600d8?ixlib=rb-4.0.3&auto=format&fit=crop&w=100&q=80",
    },
  ]);

  const [searchQuery, setSearchQuery] = useState("");
  const [showFilters, setShowFilters] = useState(false);
  const [sortConfig, setSortConfig] = useState<{ key: string; direction: "asc" | "desc" } | null>(null);

  const getFilteredSales = () => {
    let filtered = sales.filter((sale) =>
      sale.productName.toLowerCase().includes(searchQuery.toLowerCase()) ||
      sale.clientName.toLowerCase().includes(searchQuery.toLowerCase())
    );

    if (sortConfig) {
      filtered.sort((a, b) => {
        const aValue = a[sortConfig.key as keyof Sale];
        const bValue = b[sortConfig.key as keyof Sale];
        if (aValue < bValue) return sortConfig.direction === "asc" ? -1 : 1;
        if (aValue > bValue) return sortConfig.direction === "asc" ? 1 : -1;
        return 0;
      });
    }

    return filtered;
  };

  const filteredSales = getFilteredSales();

  const handleExport = () => {
    const csvContent = [
      ["ID", "Produit", "Quantité", "Couleurs", "Tailles", "Client", "Email", "Date", "Total"],
      ...filteredSales.map((sale) => [
        sale.id,
        sale.productName,
        sale.quantity,
        sale.colors.join(", "),
        sale.sizes.join(", "),
        sale.clientName,
        sale.clientEmail,
        sale.date,
        sale.total.toFixed(2),
      ]),
    ]
      .map((row) => row.join(","))
      .join("\n");

    const blob = new Blob([csvContent], { type: "text/csv;charset=utf-8;" });
    const link = document.createElement("a");
    const url = URL.createObjectURL(blob);
    link.setAttribute("href", url);
    link.setAttribute("download", "ventes_chic_et_tendance.csv");
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);

    toast.success("Ventes exportées avec succès !");
  };

  const handleSort = (key: string) => {
    let direction: "asc" | "desc" = "asc";
    if (sortConfig?.key === key && sortConfig.direction === "asc") {
      direction = "desc";
    }
    setSortConfig({ key, direction });
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-50 to-gray-200 py-12 px-4 sm:px-6 lg:px-8">
      <div className="max-w-7xl mx-auto">
        {/* Titre "Mes Ventes" avec bouton Exporter */}
        <div className="mb-6 flex items-center justify-between">
          <div className="flex items-center gap-3">
            <Store className="h-8 w-8 text-black" />
            <h1
              className={`text-3xl font-extrabold text-gray-900 tracking-tight ${montserrat.className}`}
              style={{ fontFamily: "'Montserrat', sans-serif" }}
            >
              Mes Ventes
            </h1>
          </div>
          <button
            onClick={handleExport}
            className="bg-black text-white px-6 py-2 rounded-lg flex items-center gap-2 hover:bg-gray-800 transition-all duration-300 shadow-md"
          >
            <Download className="h-5 w-5" />
            Exporter
          </button>
        </div>

        {/* Texte descriptif */}
        <p className="mb-6 text-lg text-gray-700">
          Vous trouverez ici toutes vos ventes effectuées ainsi que leur statut.
        </p>

        {/* Barre de recherche et filtres */}
        <div className="mb-8 flex flex-col items-center gap-4">
          <div className="relative w-full max-w-md">
            <input
              type="text"
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              placeholder="Rechercher par produit ou client..."
              className="w-full px-4 py-3 bg-white/30 backdrop-blur-lg border border-gray-200/50 rounded-lg focus:ring-2 focus:ring-black focus:border-black transition-all duration-300 text-gray-800 placeholder-gray-600 shadow-md"
            />
            <div className="absolute inset-y-0 right-3 flex items-center">
              <svg
                className="h-5 w-5 text-gray-500"
                fill="none"
                stroke="currentColor"
                viewBox="0 0 24 24"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth="2"
                  d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z"
                />
              </svg>
            </div>
          </div>
          <button
            onClick={() => setShowFilters(!showFilters)}
            className="p-2 bg-gray-50 border border-gray-200 rounded-lg text-gray-600 hover:bg-gray-100 transition"
          >
            <Filter className="h-4 w-4" />
          </button>
          {showFilters && (
            <div className="p-3 bg-gray-50 rounded-lg border border-gray-200 flex flex-wrap gap-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">Trier par produit</label>
                <select
                  onChange={(e) => handleSort('productName')}
                  className="px-3 py-2 bg-white border border-gray-200 rounded-md text-sm"
                >
                  <option value="">Aucun</option>
                  <option value="asc">A-Z</option>
                  <option value="desc">Z-A</option>
                </select>
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">Trier par quantité</label>
                <select
                  onChange={(e) => handleSort('quantity')}
                  className="px-3 py-2 bg-white border border-gray-200 rounded-md text-sm"
                >
                  <option value="">Aucun</option>
                  <option value="asc">Croissant</option>
                  <option value="desc">Décroissant</option>
                </select>
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">Trier par date</label>
                <select
                  onChange={(e) => handleSort('date')}
                  className="px-3 py-2 bg-white border border-gray-200 rounded-md text-sm"
                >
                  <option value="">Aucun</option>
                  <option value="asc">Plus ancien</option>
                  <option value="desc">Plus récent</option>
                </select>
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">Trier par total</label>
                <select
                  onChange={(e) => handleSort('total')}
                  className="px-3 py-2 bg-white border border-gray-200 rounded-md text-sm"
                >
                  <option value="">Aucun</option>
                  <option value="asc">Croissant</option>
                  <option value="desc">Décroissant</option>
                </select>
              </div>
            </div>
          )}
        </div>

        {/* Tableau des ventes */}
        <div className="bg-white rounded-xl shadow-lg overflow-hidden">
          <div className="overflow-x-auto">
            <table className="min-w-full divide-y divide-gray-200">
              <thead className="bg-black text-white">
                <tr>
                  <th className="px-6 py-4 text-left text-sm font-semibold">Image</th>
                  <th className="px-6 py-4 text-left text-sm font-semibold">Produit</th>
                  <th className="px-6 py-4 text-left text-sm font-semibold">Quantité</th>
                  <th className="px-6 py-4 text-left text-sm font-semibold">Couleurs</th>
                  <th className="px-6 py-4 text-left text-sm font-semibold">Tailles</th>
                  <th className="px-6 py-4 text-left text-sm font-semibold">Client</th>
                  <th className="px-6 py-4 text-left text-sm font-semibold">Date</th>
                  <th className="px-6 py-4 text-left text-sm font-semibold">Total</th>
                </tr>
              </thead>
              <tbody className="divide-y divide-gray-200">
                {filteredSales.map((sale) => (
                  <tr
                    key={sale.id}
                    className="hover:bg-gray-50 transition-all duration-200"
                  >
                    <td className="px-6 py-4">
                      <img
                        src={sale.productImage}
                        alt={sale.productName}
                        className="w-12 h-12 object-cover rounded-md"
                      />
                    </td>
                    <td className="px-6 py-4 text-sm text-gray-900">{sale.productName}</td>
                    <td className="px-6 py-4 text-sm text-gray-900">{sale.quantity}</td>
            <td className="px-6 py-4 text-sm text-gray-900">
                      <div className="flex flex-wrap gap-1">
                        {sale.colors.map((color) => (
                          <span
                            key={color}
                            className="bg-gray-200 text-gray-800 px-2 py-1 rounded-full text-xs"
                          >
                            {color}
                          </span>
                        ))}
                      </div>
                    </td>
                    <td className="px-6 py-4 text-sm text-gray-900">
                      <div className="flex flex-wrap gap-1">
                        {sale.sizes.map((size) => (
                          <span
                            key={size}
                            className="bg-gray-200 text-gray-800 px-2 py-1 rounded-full text-xs"
                          >
                            {size}
                          </span>
                        ))}
                      </div>
                    </td>
                    <td className="px-6 py-4 text-sm text-gray-900">
                      <div>
                        <span className="font-semibold">{sale.clientName}</span>
                        <p className="text-xs text-gray-600">{sale.clientEmail}</p>
                      </div>
                    </td>
                    <td className="px-6 py-4 text-sm text-gray-900">{sale.date}</td>
                    <td className="px-6 py-4 text-sm text-gray-900">{sale.total.toFixed(2)}</td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>

        {/* Message si aucune vente */}
        {filteredSales.length === 0 && (
          <div className="text-center py-12">
            <img
              src="https://cdn-icons-png.flaticon.com/512/2748/2748551.png"
              alt="Aucune vente"
              className="mx-auto w-32 h-32 mb-4"
            />
            <p className="text-xl font-semibold text-gray-800">
              Aucune vente trouvée
            </p>
            <p className="text-gray-600 mt-2">
              Aucun résultat ne correspond à votre recherche.
            </p>
          </div>
        )}
      </div>
    </div>
  );
}